@import "../../styles/mixins";

body {
    @include pureBody;
  };



.Privacy {

  p {
    margin: 5px !important;
  };

  ul {
    padding-left: 20px;
  };

  li {
    // list-style: disc !important;
    font-size: 1.2rem;
    padding-left: 1%;
  };

  h1 {
    margin-bottom: 20px;
  };

  h2 {
    margin-bottom: 20px;
  };

  h3 {
    text-align: center;
    font-size: 1.8rem;
  };

  h4 {
    text-align: center;
    font-size: 1.4rem;
    margin-bottom:10px;
  };

  #footer p {
    text-align: right;
  };

  .first_p {
    padding-bottom: 10px;
  };

  .google-link {
    color: #1E4470;
    text-decoration: underline;
  };

  margin-bottom: 2vh;
  padding: 1% 8%;
};
  
ol{
  list-style-type: none;
  counter-reset:  item;
  li::before {
    content: counter(item) ') '; /* Добавляем к числам скобку */
    counter-increment: item; /* Задаём имя счетчика */
  }
}
