.button-install {
  padding: 20px 20px;
  flex: 0 0 55%;
  align-items: center;
  justify-content: center;
  background-color: #45b8ff;
  border-radius: 50px;
  display: block;
  transition: all 0.2s ease-in-out;
  text-align: center;
  white-space: nowrap;

  &:hover {
    transform: scale(1.1);
  }

  &.disabled {
    background-color: #c4c4c4;
    pointer-events: none;
    cursor: default;

    &:hover {
      transform: scale(1);
    }
  }
}

.button-chrome {
  z-index: 2;
  font-size: 2rem;
  // width: 100%;
  display: flex;

  &-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
