@import "../../styles/variables";
@import "../../styles/mixins";

.Home {
  .container {
    margin: 4% 16.8% !important;
  }

  .hero {
    background: no-repeat bottom/cover url("../../assets/images/home/bcg3.svg");
    text-align: center;
    margin-top: -100px;
    padding-top: 100px;
  }

  .wrapper-hero {
    padding: 10% 0 20%;
  }

  /*hero*/
  .hero-title {
    margin: 0 15% 0.5%;
    font-size: 2.3rem;
    font-weight: normal;
    line-height: 3.2rem;
    color: $color;
  }

  .subtitle {
    color: $color;
    font-size: 1.5rem;
    font-weight: 100;
    margin-bottom: 3rem;
    margin-top: 1%;
  }

  .button-started {
    @include centralJustifyFlex;
    width: 100%;
    margin-top: 2%;

    &.bottom {
      margin: 0;
      transform: translateY(-50%);
    }
  }

  .get-started-button {
    display: block;
    width: auto;

    font-size: 1.4rem;
    text-transform: capitalize;
    color: $color;

    background: $blogBtnColor;
    box-shadow: 0px 15px 30px rgba(0, 0, 0, 0.15);
    border: none;
    border-radius: 50px;
    transition: all 0.3s linear;

    &:hover {
      background: $startBtnColorHover;
    }

    &.top,
    &.bottom {
      @include centralAlignFlex;
      padding: 1% 4%;

      .icon {
        padding-right: 3%;
        margin-bottom: 4%;
        flex: 0 0 auto;
        height: 3rem;
      }

      span {
        flex: 0 0 auto;
      }
    }

    &.bottom {
      font-size: 1.4rem;
    }

    &.blog-button {
      padding: 20px 20px;
      max-width: 360px;
    }
  }

  /*content*/
  .container {
    margin: 0 6%;
  }

  .content-home {
    margin: 6% 0;
    position: relative;
  }

  .enjoyIt-wrapper,
  .content-context-memory {
    display: flex;
    justify-content: space-between;
  }

  .content-context-memory {
    flex-direction: row-reverse;
    margin-top: -80px;
  }

  .enjoyIt-heading,
  .context-title {
    padding-top: 50px;
    flex: 0 1 50%;
  }

  .enjoyIt-heading {
    margin-right: 5%;
  }

  .context-title {
    margin-left: 5%;
  }

  .imgEnjoy img,
  .img-context-puzzle img {
    @include wHHundred;
  }

  .imgEnjoy,
  .img-context-puzzle {
    flex: 1 1 50%;
  }

  .app-download-wrapper {
    @include centralAlignFlex;
    flex-wrap: nowrap;
  }

  .img-phone {
    @include centralFlexPosition;
    position: relative;
    flex-basis: 50%;
  }

  .img-phone .img-phone-front {
    position: absolute;
    width: 50%;
    transform: translateX(-200%);
    transition: transform 0.8s ease-out;

    &.show {
      transform: translateX(10%);
    }
  }

  .img-phone .img-phone-back {
    width: 100%;
  }

  .content-description {
    flex-basis: 50%;
    margin-left: 5%;
    text-align: left;

    h2 {
      font-size: 2.2rem;
    }
  }

  h2 > span {
    font-weight: 800;
  }

  h2 > sup {
    font-size: 1rem;
    font-weight: 800;
  }

  .openSoftware {
    color: lightblue;
    text-decoration: underline;
    transition: 0.3s;
  }

  .openSoftware:hover {
    text-decoration: none;
    color: #4d8bdd;
  }

  .button-block {
    margin-top: 3%;
  }

  .button-group {
    @include centralAlignFlex;
    width: 100%;
    margin-top: 4px;
    position: relative;

    &-comment {
      position: absolute;
      left: 100%;
      top: 5px;
      font-size: 0.9rem;
      color: #979797;
      text-align: left;
      width: fit-content;
      margin-left: 5px;
      font-weight: 100;
    }
  }

  .button-group-1 .button-install {
    background-color: #45b8ff;
    font-size: 2rem;
  }

  .button-group-2 {
    margin-left: 2%;
  }

  .button-group-2 .button-install {
    background-color: #46b8ff;
  }

  .button-group-3 .button-install {
    background-color: #5396ef;
  }

  .button-android,
  .button-guide,
  .button-iOS {
    position: relative;
    padding: 16px 5px;
  }

  .button-iOS {
    position: relative;
    right: 30%;
    text-align: center;
    padding-left: 29%;
    padding-right: 1%;
    flex: 0 0 50%;
  }

  .button-guide {
    font-size: 0.7rem;
    opacity: 0;
    right: 8%;
    padding-left: 30px;
  }

  .button-group-1:hover .button-guide {
    opacity: 1;
  }

  .content-howItWorks h2 {
    text-align: center;
    margin-bottom: 2%;
  }

  .extension img {
    width: 100%;
  }

  .content-sources {
    margin-bottom: 1%;
    padding-bottom: 0;
  }

  .sources-wrapper {
    justify-content: center;
    text-align: left;
    display: grid;
    grid-template-areas:
      "text stickers"
      "button stickers";
    grid-template-columns: repeat(2, 50%);
    grid-template-rows: 1fr auto;
  }

  .sources-title {
    @include flexColumn;
    justify-content: flex-start;
    padding: 20px 40px 20px 0px;
    grid-area: text;

    h2 {
      margin-bottom: 0.5rem;
    }
  }

  .blog {
    padding: 20px 40px 0 0px;
    grid-area: button;

    p {
      margin-bottom: 1.5rem;
    }
  }
  .sources-block {
    grid-area: stickers;
    margin: -2%;

    &_row {
      width: 100%;
      display: flex;
    }

    .sources-paragraph {
      flex: 0 1 50%;
      margin: 2%;
      padding: 20px;
      border-radius: 25px;
      background: #45b8ff;
      color: white;
      p {
        font-size: 1rem;
        color: white;
      }
      &.disabled {
        opacity: 0;
      }
    }
  }

  .get-started-footer {
    background: url("../../assets/images/home/Illustration6(3).svg")
      bottom/cover no-repeat;
    // height: 22vw;
    // padding-top: 14%;
  }

  .get-started-footer_privacy {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    button {
      @include buttonCorrect;
      background: rgba(255, 255, 255, 0);
    }
  }

  .privacy {
    margin-top: 10px;
    color: #c9c9c9;
  }
}
