@import "../../styles/mixins";
@import "../../styles/variables";

body {
  @include pureBody;
}

/* Header of the page */
.getStartedExt-header {
  background: no-repeat bottom/cover url("../../assets/images/bcgTop.png");
  text-align: center;
  margin-top: -100px;
  padding-top: 100px;
}

.getStarted-subtitle {
  @include centralRowFlexPositionHWHundred;
  text-align: start;
}

.getStarted-subtitle li {
  margin-left: 50px;
  list-style: disc;
  font-size: 1.2rem;
}

.wrapper-getStarted {
  padding: 1% 0 4%;
}

.wrapper-getStarted img {
  width: 50%;
  height: 50%;
}

.getStarted-logo {
  text-align: center;
  padding-top: 5%;
}

.getStarted-title {
  color: $extensionInstructionColor;
  text-align: center;
  letter-spacing: 1.41px;
  margin: 1% 15% 0.5%;
  font-style: normal;
  font-size: 2.3rem;
  font-weight: 300;
  line-height: 44px;
}

.getStarted-button {
  @include centralJustifyFlex;
  // flex-direction: column;
  // height: 100%;

  width: 100%;
  margin-top: 2%;
}

.getStarted-button:nth-child(1) {
  margin: 0;
  transform: translateY(-50%);
}

.started-button {
  @include normalBoldFont;
  @include buttonCorrect;
  display: block;
  font-size: 1.125rem;
  letter-spacing: 1px;
  color: $color;
  line-height: 1.375rem;
  background: $startBtnColor;
  border-radius: 110px;
  transition: all 0.3s linear;
  width: 13vw;
  height: 4vw;

  &:hover {
    background: $startBtnColorHover;
  }

  &.top,
  &.bottom {
    @include centralAlignFlex;
    justify-content: center;
  }

  &.bottom {
    font-size: 1.4rem;
  }

  &.blog-button {
    padding: 20px 20px;
    max-width: 360px;
  }
}

.getStarted-footer {
  background: url("../../assets/images/bottom_flowers.png") bottom/contain no-repeat;
  padding: 100px;
}
