@import "https://netdna.bootstrapcdn.com/font-awesome/4.1.0/css/font-awesome.min.css";

* {
  font-family: "Montserrat", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 12px;
}

body {
  font-size: 1rem;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #555;
}

.wrapper {
  max-width: 1440px;
  padding: 0 40px;
  margin: 0 auto;
  box-sizing: border-box;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h2 {
  font-size: 2rem;
  font-weight: 500;
  word-wrap: break-word;
  word-break: keep-all;
}

h3 {
  text-transform: capitalize;
  font-size: 1.5rem;
  font-weight: normal;
}

p {
  margin: 2% 0;
  font-size: 1.2rem;
  font-weight: normal;
  line-height: 1.4;
  color: rgba(0, 0, 0, 0.72);
}

a {
  text-decoration: none;
  font-weight: normal;
  letter-spacing: 0.7px;
  color: white;
  text-align: center;
  cursor: pointer;
}

li {
  list-style: none;
}

@media screen and (min-width: 1550px) {
  html {
    font-size: 14px;
  }
}
